import React from 'react';
import './email-address.css';

import Static from '../../services/static';

export default class EmailAddress extends React.Component {
  handleChange = (e) =>
    this.props.onChange(e.target.value);

  render() {
    return (
      <section className="email-address bg-primary">
        <h1 className="section-title">Where should we send it?</h1>
        <form className="container" noValidate onSubmit={this.props.onSubmit}>
          <input
            placeholder="morty@getschwif.ty"
            type="email"
            onChange={this.handleChange}
            value={this.props.value}
          />
          <button className="btn btn--inverted" type="submit">
            Submit
          </button>
        </form>
      </section>
    );
  }
}

